
html, body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2C3E50;
}

#header {
  top: 0;
  left: 0;
  right: 0;
  height: 40px;
  background: #06150C;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 10px;
  color: white;
  width: 100%;
  margin-bottom: 20px;
}
#content {
  position: fixed;
  height: 100%;
  width: 100%;
  bottom: 0;
  background: ghostwhite;
  right: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.heading {
  color: rgb(90, 194, 113);
}
.collection-name {
  display: block;
  width: 250px;
  float: left;
}
.commander {
  left: 0;
  /*position: absolute;*/
  top: 60px;
  width: 100%;
  right: 0;
}
.operations {
  /*position: absolute;*/
  left: 0;
  right: 0;
  width: 100%;
  z-index: 9;
}
.split {
  position: relative;
  float: left;
  display: block;
  z-index: 900;
}
.db-view {
  padding: 0;
  height: 100%;
  width: 100%;
  right: 0;
  top: 0;
  bottom: 0;
  background: black;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.db-list ul li {
  cursor: pointer;
}
.db-list {
  width: 17%;
  display: block;
  background: #1D1B1B;
  color: white;
  bottom: 0;
  top: 0;
  height: 100%;
  padding: 15px;
  -webkit-box-shadow: 2px 2px 3px 1px rgba(179, 173, 173, 0.24);
          box-shadow: 2px 2px 3px 1px rgba(179, 173, 173, 0.24);
}
#logo {
  background: white;
  z-index: 6;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  width: 267px;
  height: 51px;
  position: fixed;
}
img#root-parent {
  left: 0;
  position: absolute;
  bottom: 10px;
  width: 130px;
  margin: 0 auto;
  right: 0;
}

.input {
  height: 24px !important;
  border-radius: 17px;
  width: 500px;
  border: 1px solid rgb(191, 191, 190);
  padding: 3px 11px;
  margin: 2px;
  background: white !important;
  outline: none;
}
.results-output {
  background: black;
  color: white;
  height: 100%;
  /* position: absolute; */
  width: 100%;
  left: 0;
  margin-top: 15px;
}
#skip, #limit {
  width: 100px;
}
.expanded {
  min-height: 335px;
  background: none !important;
  padding: 0 !important;
}
.split button {
  height: 40px;
  border-radius: 19px;
  outline: none;
  border: 1px;
  right: 25px;
  position: fixed;
  top: 9px;
}
.operations-command {
  margin-top: -20px;
  padding: 10px 5px;
  background: #777777EB;
  width: 100%;
  display: block;
}
.collections-root {
  width: 100%;
}
